.grid_items {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 2px solid grey;
    padding: 10px;
    border-radius: 15px;
    background-color: rgb(201, 224, 224);
    transition: transform 0.5s;
}

.grid_items>h3 {
    margin-top: 10px;
}

.grid_items:hover {
    transform: scale(1.1);
}

.skillBox {
    padding: 2%;
    border-radius: 5px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}