#projects {
    margin-top: 50px;
}

.project_box {
    margin-top: 50px;
    padding: 2%;
    border-radius: 5px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

.latest {
    width: 18%;
    background: linear-gradient(to top, rgb(235, 251, 60) 40%, white 30%);
}

.others {
    width: 30%;
    margin-top: 25px;
    background: linear-gradient(to top, rgb(235, 251, 60) 40%, white 30%);
}

.image-box {
    background-image: url("../Images/dotted.png");
    background-position: center;
    /* Center the image */
    background-repeat: no-repeat;
    /* Do not repeat the image */
    background-size: cover;
    width: 50%;
    display: flex;
    justify-content: center;
    transition: transform 0.5s;
}

.image-box>img {
    padding: 15px;
    width: 100%;
}

.image-box :hover {
    transform: scale(1.1);
}

.project-description {
    /* border: 1px solid black; */
    width: 50%;
    padding: 25px;
}

.project_heading {
    background-color: #89CFF0;
    padding: 13px;
    border-radius: 10px;
}

.project_box2 {
    padding: 2%;
    border-radius: 5px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}